  .products__list {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 85px;
  }
}

.products__card {
  display: block;
  min-width: 270px;
  min-height: 250px;
}
@media (min-width: 768px) {
  .products__card {
    min-width: 670px;
    height: 250px;
  }
}
@media (min-width: 1024px) {
  .products__card {
    min-width: 350px;
    height: 650px;
  }
}

.products__name {
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 30px;
  color: #FFFFFF;
}

.products__link {
  text-decoration: none;
}

.products__icon {
  display: block;
}

.products__card--one {
  background-image: url("../img/card1.png");
}
@media (min-width: 768px) {
  .products__card--one {
    background-image: url("../img/card1tablet.png");
  }
}

.products__card--two {
  background-image: url("../img/card2.png");
}
@media (min-width: 768px) {
  .products__card--two {
    background-image: url("../img/card2tablet.png");
  }
}

.products__card--three {
  background-image: url("../img/card3.png");
}
@media (min-width: 768px) {
  .products__card--three {
    background-image: url("../img/card3tablet.png");
  }
}

